.button {
  width: 12.625rem;
  font-family: "Gotu", sans-serif;
  font-size: 0.875rem;
  color: #fff;
  font-weight: 400;
  line-height: 1.5714285714;
  display: inline-block;
  text-align: center;
  padding: 1rem 0;
  background-color: #08AAC6;
  border: 1px solid #08AAC6;
  border-radius: 0.5rem;
  position: relative;
  z-index: 1;
  transition: 0.3s;
}

.button::first-letter {
  text-transform: capitalize;
}

.button:hover span,
.button:hover span::after {
  color: #08AAC6;
  border-color: #08AAC6;
}

.button::before {
  content: "";
  display: block;
  border: 1px solid #08AAC6;
  border-radius: 0.5rem;
  width: 100%;
  height: 100%;
  transition: 0.3s;
  position: absolute;
  top: 0.25rem;
  left: 0.25rem;
  z-index: -2;
}

.button::after {
  content: "";
  width: 100%;
  max-width: 100%;
  height: 100%;
  background: #fff;
  border-radius: 0.5rem;
  position: absolute;
  top: 0;
  left: 0;
  transform: scale(0, 1);
  transform-origin: left top;
  transition: 0.2s cubic-bezier(0.45, 0, 0.55, 1);
  z-index: -1;
}

.button:hover::after {
  transform: scale(1, 1);
}

.button span {
  font-family: "Gotu", sans-serif;
  font-size: 0.875rem;
  color: #fff;
  font-weight: 400;
  line-height: 1.5714285714;
  padding-right: 2.5rem;
}

.button span::after {
  content: "";
  display: inline-block;
  width: 2.5rem;
  max-width: 100%;
  height: 0.375rem;
  border-right: 1px solid #fff;
  border-bottom: 1px solid #fff;
  transform: skew(45deg);
  margin-left: 0.5rem;
  position: absolute;
  top: 42.857%;
  right: 2.5rem;
  z-index: 1;
}

